
function translateErrorMessages(errorCode: string): string {
    console.log(errorCode);
    switch (errorCode) {
      case 'auth/expired-action-code':
        return 'O cdigo da ao ou o link expirou.';
      case 'auth/invalid-action-code':
        return 'O cdigo da ao  invlido. Isso pode acontecer se o cdigo estiver malformado ou j tiver sido usado.';
      case 'auth/user-disabled':
        return 'O usurio correspondente  credencial fornecida foi desativado.';
      case 'auth/user-not-found':
        return 'O usurio no correponde  nenhuma credencial.';
      case 'auth/weak-password':
        return 'A senha  muito fraca.';
      case 'auth/email-already-in-use':
        return 'J existe uma conta com o endereo de email fornecido.'; 
      case 'auth/invalid-email':
        return 'O endereo de e-mail no  vlido.';
      case 'auth/operation-not-allowed':
        return 'O tipo de conta correspondente  esta credencial, ainda no encontra-se ativada.';
      case 'auth/account-exists-with-different-credential':
        return 'E-mail j associado a outra conta.';
      case 'auth/credential-already-in-use':
        return 'J existe uma conta para esta credencial.';
      case 'auth/invalid-credential':
        return 'A credencial expirou ou est mal formada.';
      case 'auth/wrong-password':
        return 'Senha incorreta.';
      case 'auth/invalid-custom-token':
        return 'O token fornecido no  vlido.';
      case 'auth/captcha-check-failed':
        return 'O token de resposta do reCAPTCHA no  vlido, expirou ou o domnio no  permitido.';
      case 'auth/invalid-email-verified':
        return 'O e-mail  invlido.';
      case 'auth/invalid-password':
        return 'A senha  invlida, precisa ter pelo menos 6 caracteres.';
      case 'auth/email-already-exists':
        return 'O e-mail que voc tentou cadastrar j est em uso.';
      default:
        return 'Ocorreu um erro, por favor tente novamente';
    }
  }


function errorMessages(code: number){
    switch(code){
        case 1:
            return "Sinopse no disponvel"
        default:
            return "" // um erro genrico
    }
}

function maxText(text: string | undefined | null, max: number) {
    if(!checkIfSinopse(text)){
        return errorMessages(1);
     } 
    const textFormatted = text?.slice(0,max)
    return textFormatted;
}

function checkIfSinopse(text: string | undefined | null){
    if(!text){
        return false;
    }
    return true;
}


export {maxText, checkIfSinopse, errorMessages, translateErrorMessages}